@font-face {
  font-family: 'Roboto Slab';
  src: local('Roboto Slab Regular'), local('RobotoSlab-Regular'),
    url('Robotoslabregular.woff2') format('woff2'),
    url('Robotoslabregular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Roboto Slab';
  src: local('Roboto Slab Medium'), local('RobotoSlab-Medium'),
    url('Robotoslabmedium.woff2') format('woff2'),
    url('Robotoslabmedium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Roboto Slab';
  src: local('Roboto Slab Bold'), local('RobotoSlab-Bold'),
    url('Robotoslabbold.woff2') format('woff2'),
    url('Robotoslabbold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
